:root {
	--fgNav: #a5acbb;
}

html {
	scroll-behavior: smooth;
	height: 100%;
}

body {
	position: relative;
	font: normal 16px/1.9em "Inter", "Source Sans Pro", "Helvetica Neue", Arial,
		sans-serif;
	margin: 0;
	padding: 0;
	height: 100%;
}

h1 {
	font-style: normal;
	font-size: 45px;
	font-weight: normal;
	vertical-align: baseline;
	margin: 0;
	padding: 0;
}

a {
	color: inherit;
}
/* 
p {
    margin-bottom: 2em
} */

.center {
	text-align: center;
}

.siteHead-logoMark {
	height: 13px;
	width: 24px;
	position: relative;
	top: 2px;
	fill: currentColor;
}

.site-name {
	font-size: 11px;
	text-transform: uppercase;
	letter-spacing: 0.26em;
	font-weight: 800;
	padding-top: 2.9vmin;
	text-align: center;
	font-family: Inter, Source Sans Pro, Helvetica Neue, Arial, sans-serif;
	z-index: 1;
	top: 2px;
	position: relative;
	color: var(--fgNav);
}

@media all and (max-width: 520px) {
	.site-name {
		font-size: 10px;
	}
}

.siteLogoLink {
	color: inherit;
}

.siteLogoLink:hover {
	color: inherit;
}

.site-name-post {
	color: #fff;
}

.site-name-link {
	text-decoration: none;
	transition: color 0.2s ease-in-out;
	transform: translateZ(0);
	position: relative;
	color: inherit;
}

.site-name-link:hover {
	color: inherit;
}

.site-name-link::before {
	content: "";
	position: absolute;
	width: 100%;
	height: 2px;
	bottom: -2px;
	left: 0;
	background-color: currentColor;
	visibility: hidden;
	transform: scaleX(0);
	transition: all 0.2s cubic-bezier(0.19, 1, 0.22, 1) 0s;
}

.site-name-link:hover::before {
	visibility: visible;
	transform: scaleX(1);
}

.site-name-link-parent {
	border-right: 1px solid currentColor;
	padding-right: 5px;
	margin-right: 9px;
	text-decoration: none;
	transition: opacity 0.2s ease-out;
	opacity: 0.8;
}

.site-name-link-parent:hover {
	opacity: 0.99;
}

.site-name-link-parent-inverted {
	color: #ccc;
}

.site-name-link-parent-inverted:hover {
	color: #fff;
}

.site-name-link-parent-inverted::before {
	background-color: #fff;
}

.ampersand {
	font-family: Baskerville, serif;
	font-style: italic;
}

.single-page-header {
	font-size: 28px;
	font-weight: 400;
	line-height: 1.3em;
}

.text-highlighted {
	color: #ca6055;
}

.wrapper {
	position: relative;
	margin: 0 auto;
}

.button-clear {
	border: none;
	background-color: transparent;
}

.button-clear:focus {
	outline: 0;
}

.text-pink {
	color: #e8c3b9;
}

.text-darkgreen {
	color: #244a53;
}

.text-red {
	color: #bf615e;
}

.text-lightblue {
	color: #99bec7;
}

.text-darkblue {
	color: #2b2f3c;
}

@font-face {
	font-family: "Inter";
	font-weight: 100 900;
	font-display: swap;
	font-style: normal;
	src: url("assets/fonts/Inter.woff2") format("woff2");
}

.site-nav-canvas {
	width: 100vw;
	height: 100vh;
	position: fixed;
	z-index: 2001;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: none;
}

.menu {
	position: absolute;
	top: 2.7vmin;
	left: 2.8vmin;
	z-index: 2004;
	outline: none;
	-webkit-tap-highlight-color: transparent;
	width: 43px;
	padding: 10px;
	cursor: pointer;
}

.menu-hidden {
	visibility: hidden;
	pointer-events: none;
}

.menu-icon-line {
	display: block;
	height: 2px;
	width: 100%;
	background-color: #a5acbc;
	margin-bottom: 4px;
	border-radius: 2px;
	transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275),
		opacity 0.2s ease-out;
	transform: translateZ(0);
}

.menu:hover > .menu-icon-line {
	background-color: #8591ac;
	transform: scaleX(1.2);
}

.menu-active > .menu-icon-line-1,
.menu-active:hover > .menu-icon-line-1 {
	transform: translateY(6px) rotateZ(-135deg);
	background-color: #fff;
}

.menu-active > .menu-icon-line-2,
.menu-active:hover > .menu-icon-line-2 {
	opacity: 0;
	background-color: #fff;
}

.menu-active > .menu-icon-line-3,
.menu-active:hover > .menu-icon-line-3 {
	transform: translateY(-6px) rotateZ(135deg);
	background-color: #fff;
}

.site-search {
	position: absolute;
	top: 2vmin;
	right: 3vmin;
	z-index: 2003;
	padding: 10px 5px;
	cursor: pointer;
}

.site-search-icon {
	display: block;
	transition: opacity 0.2s ease-out;
}

.site-search-icon:hover .site-search-icon-circle {
	stroke: #8591ac;
}

.site-search-icon:hover .site-search-icon-line {
	fill: #8591ac;
}

.site-search-icon-circle {
	stroke: #a5acbc;
}

.site-search-icon-svg {
	display: block;
}

.site-search-icon-line {
	fill: #a5acbc;
}

.site-search-close-icon {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	opacity: 0;
}

.site-search-close-icon-line {
	position: absolute;
	height: 2px;
	width: 70%;
	left: 0;
	right: 0;
	background-color: #fff;
	border-radius: 2px;
	top: 40%;
	margin: auto;
	transform: rotateZ(45deg) scale(0.8);
	opacity: 1;
}

.site-search-content-results {
	text-align: left;
}

.site-search-results-item {
	color: inherit;
	text-decoration: none;
	font-weight: 600;
	font-size: 42px;
	display: block;
	padding: 40px;
	line-height: 1.4em;
	text-overflow: ellipsis;
	overflow: hidden;
}

.site-search-results-item:hover {
	color: inherit;
}

@media (max-width: 700px) {
	.site-search-results-item {
		font-size: 6vw;
		padding: 20px;
	}
}

.site-search-results-item-active {
	color: inherit;
	background-color: rgba(255, 255, 255, 0.1);
}

.site-search-results-item-desc {
	font-size: 14px;
	display: block;
	font-weight: 400;
	padding: 10px 0;
	max-width: 500px;
	line-height: 1.4em;
}

.site-search-content {
	margin: 20px auto;
	padding: 0 20px;
	width: 80%;
	max-width: 1200px;
}

@media (max-width: 700px) {
	.site-search-content {
		width: 90%;
		padding-left: 0;
		padding-right: 0;
	}
}

.siteSearchInput {
	background-color: transparent;
	border-radius: 0;
	border: none;
	border-bottom: 2px solid rgba(255, 255, 255, 0.1);
	display: block;
	font-size: 24px;
	width: 100%;
	padding: 20px 20px 20px 0;
