	-webkit-tap-highlight-color: transparent;
}

.composition h1 {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0.5em;
	left: 0;
	margin: auto;
	height: 1em;
}

.composition:hover .composition-hint {
	opacity: 1;
}

@media (max-width: 940px) {
	.composition {
		flex: 1 100%;
	}
}

@media (max-height: 570px) {
	.composition {
		height: 420px;
	}
}

.composition-hint {
	font-weight: 400;
	padding-bottom: 20px;
	display: inline-block;
	opacity: 0;
	transform: translateZ(0);
	transition: opacity 0.3s ease-in-out;
	position: absolute;
	bottom: 0;
	right: 0;
	left: 0;
}

.composition-hint-arrows {
	vertical-align: middle;
	position: relative;
	top: -1px;
	padding-right: 5px;
}

.composition-wrapper {
	transform: translateZ(0);
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
}

.composition-active {
	width: 50%;
	right: auto;
	left: 0;
	z-index: 3000;
	position: fixed;
}

@media (max-width: 940px) {
	.composition-active {
		width: 100%;
		height: 480px;
	}
}

.compositions-overlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #000;
	opacity: 0;
	z-index: 2000;
	display: none;
}

.composition-source {
	position: fixed;
	height: 100%;
	width: 100%;
	top: 0;
	bottom: 0;
	right: 0;
	margin: auto;
	z-index: 3001;
	display: none;
	text-align: left;
	color: #000;
	box-sizing: border-box;
	overflow-y: scroll;
	padding-left: 50%;
}

@media (max-width: 480px) {
	.composition-source {
		-webkit-overflow-scrolling: touch;
	}
}

@media (max-width: 940px) {
	.composition-source {
		padding-left: 0;
		padding-top: 500px;
		margin-top: 0;
	}
}

@media (max-height: 570px) and (max-width: 940px) {
	.composition-source {
		padding-top: 430px;
	}
}

.composition-source-container {
	display: flex;
	place-items: center;
	background-color: #fff;
	transform-origin: 100% 0;
	overflow-x: hidden;
	overflow-x: hidden;
	min-height: 100%;
}

@media (max-width: 940px) {
	.composition-source-container {
		padding-top: 0;
		margin-top: 0;
		place-items: start;
	}
}

@media (max-width: 480px) {
	.composition-source-container {
		padding-top: 20px;
	}
}

/* .composition-source-2 {
    text-align: initial;
} */

.composition-source-header {
	width: 100%;
	font-size: 86px;
	font-weight: 800;
	padding-top: 100px;
	padding-bottom: 100px;
	line-height: 0.3em;
	display: none;
	padding: 40px 20px 40px 40px;
}

.composition-source-header-active {
	display: block;
}

@media (max-width: 940px) {
	.composition-source-header {
		padding-top: 30px;
		padding-bottom: 30px;
		font-size: 54px;
		line-height: 0.5em;
	}
}

@media (max-width: 480px) {
	.composition-source-header {
		/* display:none */
	}
}

.composition-source-text {
	font-size: 0.8rem;
	letter-spacing: 0.05em;
	color: #adadad;
	padding-bottom: 20px;
	margin: 0;
	border-bottom: 1px solid #eee;
	font-weight: 600;
}

.composition-source-subheader-container {
	display: flex;
	justify-content: space-between;
	gap: 15%;
}

.composition-source-subheader {
	font-size: 0.833rem;
	color: #adadad;
	text-transform: uppercase;
	font-weight: 800;
}

.composition-source-subheader-right {
	text-align: right;
	text-transform: capitalize;
}

.composition-source-subheader-left {
	text-align: left;
	text-transform: capitalize;
}

.composition-source-code {
	border: 0;
	cursor: text;
	font: 14px Consolas, "Courier New", Courier, monospace;
	outline: 0;
	resize: none;
}

.highlight {
	background-color: #f4f4f6;
	border-top: 1px solid #eee;
	border-bottom: 1px solid #eee;
	padding: 2em 2em 2em 40px;
	position: relative;
	width: 100%;
	overflow-x: scroll;
	margin-bottom: 2em;
	line-height: 1.5em;
	box-sizing: border-box;
}

@media (max-width: 480px) {
	.highlight {
		overflow-x: auto;
		overflow-wrap: break-word;
	}
}

.hidden {
	display: none;
}

.composition-back-button {
	position: fixed;
	top: 20px;
	left: 20px;
	z-index: 3002;
	display: none;
	-webkit-tap-highlight-color: transparent;
}

.composition-back-image {
	width: 51px;
	height: 51px;
}

@media (max-width: 940px) {
	.composition-back-button {
		position: absolute;
	}
}

.collection-inline-header {
	font-weight: 200;
}

@media (max-width: 420px) {
	.collection-inline-header {
		text-align: center;
	}
}
